  margin-left: 200px;
}
body{
  overflow:auto;
}
.img-border{
  border-radius: 30px;
}
a{
  color: aliceblue;
}
.App{
  background-image: linear-gradient(to top, #cfd9df 0%, #e2ebf0 100%);
}